.success {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 100;
  background: linear-gradient(180deg, rgba(25, 59, 103, 0.05) 9.72%, rgba(25, 59, 103, 0) 101.07%), #FFFFFF;
  &__header {
    height: 178px;
    background: linear-gradient(97.02deg, #8B75FF 1.89%, #6A89FF 100%);
    position: relative;
  }
  &__iconContainer {
    position: absolute;
    bottom: 0;
    left: 50%;
    border-radius: 100%;
    transform: translate(-50%, 50%);
    background: white ;
  }
  &__icon {
    filter: invert(37%) sepia(97%) saturate(1014%) hue-rotate(116deg) brightness(92%) contrast(103%);
  }
  &__content {
    margin-top: 80px;
    display: flex;
    flex-direction: column;
  }
  &__title {
    font-size: 18px;
    line-height: 125.3%;    
    text-align: center;
    color: rgba(27, 43, 65, 0.72);
    &-black {
      display: inline;
      color: #192434;
    }
  }
  &__btns {
    width: 210px;
    margin: 24px auto;
  }
  &__btn {
    padding: 0;
    width: 210px;
    height: 48px;
    background: #1E4DFC;
    font-size: 16px;
    line-height: 19px;
    &-back {
      color: #1E4DFC;
      background: none;
    }
  }
}