@import '../../../utils/breakpoints';
@keyframes openPageTranslateFromTop {
  from {
    opacity: 0;
    transform: translateY(-50%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.transfer {
  padding: 24px 77px 0px;
  background: linear-gradient(180deg, rgba(25, 59, 103, 0.05) 0%, rgba(25, 59, 103, 0) 100%), #FFFFFF;
  &__title {
    line-height: 125.3%;
    letter-spacing: -0.01em;
    color: #192434;
    font-size: 28px;
  }
  &__subtitle, &__secondTitle {
    font-size: 14px;
    line-height: 134%;
    color: rgba(24, 39, 58, 0.94);
  }
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__field {
    margin-top: 20px;
  }
  &__input {
    margin-top: 8px;
  }
  &__inputTitle {
    color: rgba(27, 43, 65, 0.72);
  }
  &__btns {
    margin-top: 32px;
  }
  &__btn {
    font-size: 16px;
    margin-bottom: 8px;
    &_cancel {
      color: #FF4238;
      background: none;
    }
  }
  &__contents {
    margin-top: 24px;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(70px, auto));
    align-items: center;
    margin-top: 8px;
    &_next {
      margin-top: 42px;
    }
    .content {
      &__title {
        font-size: 13px;
        line-height: 125.3%;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: rgba(28, 46, 69, 0.6);
      }
      &__text {
        font-size: 16px;
        line-height: 161.1%;
        color: #192434;
      }
    }
  }
  .info {
    margin-top: 8px;
    display: flex;
    &__title {
      margin-left: 10px;
      font-size: 11px;
      line-height: 13px;   
      color: rgba(27, 43, 65, 0.72);
    }
  }
}


@media (max-width: 1050px) { 
  .transfer {
    padding: 30px;
    &__title {
      font-size: 22px;
    }
  }
}

@media (max-width: $mobile-width) { 
  .transfer {
    animation: openPageTranslateFromTop ease-in-out 0.6s forwards;
    padding: 20px;
    &__header {
      display: none;
    }
    &__secondTitle {
      display: none;
    }
  }
}